body {
  color: white;
  background: #221C37;
  font-family: 'Comfortaa', cursive;
  font-size: 14px;
  font-weight: bold;
  max-height: 100vh;
  height: 100%;
  margin: auto; }

.header {
  min-height: 72px;
  padding: 40px 0;
  display: flex;
  justify-content: center;
  align-items: flex-start; }
  @media (max-width: 781px) {
    .header {
      align-items: center; } }
  .header__image {
    margin-right: 10px; }
  .header__heading {
    font-size: 32px;
    margin: 0; }
    .header__heading--orange {
      color: #FF715B; }
      @media (max-width: 781px) {
        .header__heading--orange {
          display: block; } }
    @media (max-width: 781px) {
      .header__heading {
        font-size: 20px;
        width: 75px; } }

.main {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 6fr;
  margin: 0 16px 40px 16px;
  grid-gap: 0px 32px;
  grid-template-areas: "nav nav nav nav" "note1 note1 note2 note2"; }
  @media (max-width: 781px) {
    .main {
      grid-template-areas: "nav nav nav nav" "note note note note"; } }
  .main .navigation {
    width: 100%;
    grid-area: nav;
    text-align: center; }
    .main .navigation__list {
      list-style-type: none;
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: center; }
    .main .navigation__item {
      display: inline-block;
      margin: 0 16px 40px 16px;
      padding: 12px 0;
      text-align: center;
      border-bottom: 2px solid white;
      font-size: 20px;
      max-width: 544px;
      width: 100%;
      font-size: 14px; }
      @media (max-width: 781px) {
        .main .navigation__item {
          margin: 0 0 40px 0; } }
      @media (max-width: 781px) {
        .main .navigation__item--mobile {
          opacity: .7; } }
    .main .navigation__link {
      text-decoration: none;
      color: white; }
      @media (max-width: 781px) {
        .main .navigation__link {
          display: none; } }
    .main .navigation__edit {
      text-decoration: none;
      color: white;
      display: none; }
      @media (max-width: 781px) {
        .main .navigation__edit {
          display: block; } }
    .main .navigation__note {
      text-decoration: none;
      color: white;
      display: none; }
      @media (max-width: 781px) {
        .main .navigation__note {
          display: block; } }
  .main .note {
    max-width: 544px;
    width: 100%; }
    .main .note--left {
      justify-self: end;
      width: 100%;
      grid-area: note1; }
      @media (max-width: 781px) {
        .main .note--left {
          grid-area: note;
          justify-self: center; } }
    .main .note--right {
      justify-self: start;
      width: 100%;
      grid-area: note2; }
      @media (max-width: 781px) {
        .main .note--right {
          grid-area: note;
          justify-self: center; } }
    @media (max-width: 781px) {
      .main .note--invisable {
        display: none; } }
    .main .note__heading {
      display: none; }
    .main .note__link {
      text-decoration: none;
      color: white; }
    .main .note__form {
      max-width: 355px;
      width: 100%;
      margin: 0 auto; }
      @media (max-width: 781px) {
        .main .note__form {
          max-width: 310px;
          display: flex;
          flex-direction: column; } }
    .main .note__label {
      display: block;
      margin-bottom: 10px; }
    .main .note__input {
      width: 100%;
      margin-bottom: 20px;
      padding: 16px;
      border-radius: 4px; }
      .main .note__input::placeholder {
        font-family: 'Open Sans', sans-serif; }
    .main .note__textarea {
      width: 100%;
      margin-bottom: 20px;
      padding: 16px;
      border-radius: 4px;
      min-height: 240px; }
      .main .note__textarea::placeholder {
        font-family: 'Open Sans', sans-serif; }
    .main .note__delete {
      max-width: 165px;
      width: 100%;
      padding: 16px;
      border-radius: 4px;
      cursor: pointer;
      margin-right: 20px;
      color: #FF715B;
      background: transparent;
      border: 1px solid #FF715B; }
      @media (max-width: 781px) {
        .main .note__delete {
          max-width: 310px;
          order: 1; } }
    .main .note__save {
      max-width: 165px;
      width: 100%;
      padding: 16px;
      border-radius: 4px;
      cursor: pointer;
      background: #FF715B;
      border: 1px solid #FF715B;
      color: white; }
      @media (max-width: 781px) {
        .main .note__save {
          max-width: 310px;
          margin-bottom: 20px; } }
    .main .note .saved {
      max-width: 355px;
      width: 100%;
      margin: 0 auto; }
      @media (max-width: 781px) {
        .main .note .saved {
          max-width: 310px; } }
      .main .note .saved__unsaved {
        opacity: .5;
        text-align: center; }
        .main .note .saved__unsaved--hide {
          display: none; }
      .main .note .saved__heading {
        font-size: 30px;
        font-weight: bold;
        color: #FF715B;
        overflow-wrap: break-word; }
      .main .note .saved__text {
        opacity: .8;
        max-height: 300px;
        overflow: scroll;
        overflow-wrap: break-word; }

.footer {
  text-align: center;
  width: 100%; }
